.skillsBlock {
    min-height: 100vh;
    background-color: #343232;
    display: flex;
    align-items: center;
    justify-content: center;
    /*padding: 20px;*/
    /*box-sizing: border-box;*/
}
.skillsContainer {
    padding: 20px;
    box-sizing: border-box;
    border: 1px solid;
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    width: 80%;
}

.title {
    border: 1px solid;
    border-radius: 6px;
    width: 130px;
    height: 10%;
    color: bisque;
    text-align: center;
    font-size: 30px;

}

.skills {
    border: 1px solid;
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
}
